#ucapan {
  width: 100%;
  max-width: 1000px;
  background-size: cover;
  background-position: top;
  text-align: center;

  .ucapan-container {
    margin-top: 120px;
    width: 80%;

    @media screen and (max-width: 700px) {
      margin-top: 100px;
    }

    h2 {
      font-family: 'Philosopher', sans-serif;
      color: #113468;
      font-size: 3rem;
    }

    p {
      font-family: 'Roboto Slab', serif;
      font-size: 1.5rem;
      color: #626262;
    }

    .form-container {
      margin-top: 100px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      
      form {
        background-color: white;
        padding: 20px;
        border-top-left-radius: 7px;
        border-top-right-radius: 7px;
        border: 1px solid #d9dceb;
        border-bottom: none;

        input[type="text"], textarea {
          padding: 10px;
          width: 100%;
          border-radius: 7px;
          border: 1px solid #d9dceb;

          &:focus {
            outline: none;
            border-color: #64B6EC;
          }
        }

        select {
          color: #44525f;
          outline: none;
        }

        input[type="submit"] {
          align-self: flex-start;
          color: white;
          padding: 5px 20px;
          background-color: #113468;
          border: none;
          border-radius: 10px;
          font-size: 0.9rem;
          font-weight: 600;
        }
      }

      .daftar-ucapan {
        width: 100%;
        background-color: white;
        padding: 20px;
        border-radius: 0 0 7px 7px;
        border: 1px solid #d9dceb;

        margin-bottom: 50px;
      }
    }
  }
}