#galeri {
  max-width: 1000px;
  background-color: #113468;
  padding-bottom: 130px;

  h2 {
    margin: 30px 0;
    color: white;
    font-family: 'Great Vibes', cursive;
    text-align: center;
    font-size: 3rem;
  }

  #carouselGaleri {
    height: 450px;

    @media screen and (max-width: 450px) {
      height: 300px;
    }

    .carousel-indicators {
      height: 120px;
      width: 100%;
      padding-left: 10px;
      padding-right: 10px;
      top: 450px;
      background-color: #113468;
      overflow: scroll;
      
      @media screen and (max-width: 450px) {
        top: 300px;

        &.scroll-md {
          overflow: scroll;
          justify-content: flex-start;
        }
      }

      button {
        min-width: 120px;
        width: 120px;
        height: 100px;
      }
    }

    .carousel-inner {
      
      .carousel-item {
        // background-color: #113468;

        img {
          // height: 450px;
          // background-color: #113468;
        }
      }
    }
  }
}