#live {
  max-width: 1000px;

  .live-container {
    margin-top: 50px;
    color: #696969;

    h2 {
      font-family: "playball", Sans-serif;
      font-size: 2.5rem;
    }

    p {
      font-family: 'Alata', sans-serif;
    }

    & > div {
      max-width: 800px;
      margin: 0 auto;

      .bg-light {
        --bs-bg-opacity: 0.7;
      }

      .btn {
        background-image: linear-gradient(170deg, #6F50C9 0%, #FC5343 100%) !important;
        font-size: 0.9rem;
        font-weight: bold;
      }
    }
  }

  .line {
    padding: 0 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    max-width: 450px;
    color: #AE8F7A;

    hr {
      flex: 1;
      border: 2px solid #AE8F7A;
      opacity: 1;
    }

    svg {
      font-size: 1.5rem;
    }
  }
}

.bg-image {
  background-position: top;
}