#intro {
  background-size: cover;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;

  padding: 50px 0;
  text-align: center;

  .intro-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h1 {
      font-family: "Open Sans", sans-serif;
      color: #143b71;
      font-size: 1.5rem;
      font-weight: 600;
    }

    h2 {
      font-family: "Playball", cursive;
      color: #113468;
      font-size: 2.5rem;
    }

    p {
      font-family: "Roboto", sans-serif;
      color: #484848;
    }

    #timer {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      max-width: 800px;
      color: white;

      .box {
        background-color: #113468;
        border-radius: 23px;

        span {
          font-family: "Roboto Slab", serif;
        }
      }
    }
  }
}