#mempelai {
  width: 100%;
  max-width: 1000px;
  background-size: cover;
  background-position: top;
  padding: 50px 0;
  text-align: center;

  .mempelai-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h2 {
      font-family: "Satisfy", cursive;
      font-size: 2rem;
      font-weight: 500;
      color: #586472;
    }

    p {
      width: 80%;
      font-size: 0.9rem;
      color: #696969;
    }

    .calon-mempelai {
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      color: #7a7a7a;
  
      h3 {
        font-family: "Great Vibes", cursive;
        color: #586472;
        font-size: 2.5rem;
      }
  
      .and {
        display: block;
        font-family: "Great Vibes", cursive;
        font-size: 2rem;
        margin: 20px 0;
      }
  
      button {
        background-color: #113468;
        color: white;
  
        border-radius: 50%;
  
        svg {
          font-size: 1.2rem;
        }
      }
    }
  }
}