#acara {
  .bg-image {
    max-width: 1000px;
    margin: 0 auto;
  }

  .batas {
    width: 100%;
    background-color: #113468;

    img {
      margin-bottom: 40px;
    }

    h2 {
      font-family: "Great Vibes", Sans-serif;
      font-size: 2.5rem;
    }
  }

  .shadow {
    max-width: 800px;

    h3 {
      font-family: "Great Vibes", Sans-serif;
      color: #daab7f;
      font-size: 3rem;
    }
  
    p {
      font-family: 'Lato', sans-serif;
      color: #7a7a7a;
      font-size: 1.1rem;
    }
  
    button {
      background-color: #47699c;
      font-size: 0.8rem;
      font-weight: bold;
      padding: 5px 15px;
    }
  }
}