#amplop {
  max-width: 1000px;
  width: 100%;

  background-size: cover;
  background-position: top;

  .amplop-container {
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;

    h2 {
      font-family: 'Playball', cursive;
      color: #113468;
      font-size: 2.5rem;
    }

    p {
      color: #696969;
      font-family: 'Alata', sans-serif;
      font-size: 1.3rem;
      max-width: 750px;
    }

    .rekening {
      width: 95%;
      align-items: center;
      box-shadow: 0px 20px 25px -16px rgb(0 0 0 / 50%);

      .divider {
        width: 100%;
        height: 3px;
        background-color: #C87B3E;
      }

      .btn {
        background-color: #113468;
        color: white;
        border-radius: 20px;
        font-size: 0.9rem;

        svg {
          font-size: 1.2rem;
        }
      }
    }
  }
}

#quote {
  width: 100%;
  background-color: #113468;
  color: white;
  padding: 70px 0;
  text-align: center;

  p {
    font-family: 'Cuprum', sans-serif;
    font-weight: 500;
    font-size: 1.5rem;
    max-width: 800px;
    margin: 0 auto;
    line-height: 1.1;
  }
}