@import url(https://fonts.googleapis.com/css2?family=Alata&family=Cuprum:wght@500&family=Dosis:wght@500&family=Great+Vibes&family=Lato:ital@1&family=Nunito&family=Open+Sans:wght@400;500&family=Philosopher&family=Playball&family=Roboto&family=Roboto+Slab&family=Satisfy&display=swap);
.App{width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.bg-image{background-size:cover;background-position:center;background-repeat:no-repeat}.f-lato{font-family:"Lato",sans-serif !important}
#cover{display:flex;height:100vh;width:100%;position:fixed;top:0;z-index:5;transition:all .5s ease-in;background-repeat:no-repeat;background-size:cover;background-position:center}#cover.open{top:100vh}#cover .cover-container{width:100%;background-color:rgba(0,0,0,.48);display:flex;flex-direction:column;justify-content:center;align-items:center;color:#fff}#cover .cover-container img{display:block;width:350px;object-fit:cover}#cover .cover-container h1{font-family:"playball",Sans-serif;color:#ecf08d;font-size:3rem}#cover .cover-container p{font-family:"Roboto",Sans-serif}#cover .cover-container button{font-family:"Roboto",Sans-serif;background-color:#113468}
#intro{background-size:cover;width:100%;max-width:1000px;margin:0 auto;padding:50px 0;text-align:center}#intro .intro-container{width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}#intro .intro-container h1{font-family:"Open Sans",sans-serif;color:#143b71;font-size:1.5rem;font-weight:600}#intro .intro-container h2{font-family:"Playball",cursive;color:#113468;font-size:2.5rem}#intro .intro-container p{font-family:"Roboto",sans-serif;color:#484848}#intro .intro-container #timer{width:100%;display:flex;justify-content:center;align-items:center;max-width:800px;color:#fff}#intro .intro-container #timer .box{background-color:#113468;border-radius:23px}#intro .intro-container #timer .box span{font-family:"Roboto Slab",serif}
#mempelai{width:100%;max-width:1000px;background-size:cover;background-position:top;padding:50px 0;text-align:center}#mempelai .mempelai-container{width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}#mempelai .mempelai-container h2{font-family:"Satisfy",cursive;font-size:2rem;font-weight:500;color:#586472}#mempelai .mempelai-container p{width:80%;font-size:.9rem;color:dimgray}#mempelai .mempelai-container .calon-mempelai{width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;color:#7a7a7a}#mempelai .mempelai-container .calon-mempelai h3{font-family:"Great Vibes",cursive;color:#586472;font-size:2.5rem}#mempelai .mempelai-container .calon-mempelai .and{display:block;font-family:"Great Vibes",cursive;font-size:2rem;margin:20px 0}#mempelai .mempelai-container .calon-mempelai button{background-color:#113468;color:#fff;border-radius:50%}#mempelai .mempelai-container .calon-mempelai button svg{font-size:1.2rem}
#acara .bg-image{max-width:1000px;margin:0 auto}#acara .batas{width:100%;background-color:#113468}#acara .batas img{margin-bottom:40px}#acara .batas h2{font-family:"Great Vibes",Sans-serif;font-size:2.5rem}#acara .shadow{max-width:800px}#acara .shadow h3{font-family:"Great Vibes",Sans-serif;color:#daab7f;font-size:3rem}#acara .shadow p{font-family:"Lato",sans-serif;color:#7a7a7a;font-size:1.1rem}#acara .shadow button{background-color:#47699c;font-size:.8rem;font-weight:bold;padding:5px 15px}
#live{max-width:1000px}#live .live-container{margin-top:50px;color:dimgray}#live .live-container h2{font-family:"playball",Sans-serif;font-size:2.5rem}#live .live-container p{font-family:"Alata",sans-serif}#live .live-container>div{max-width:800px;margin:0 auto}#live .live-container>div .bg-light{--bs-bg-opacity: 0.7}#live .live-container>div .btn{background-image:linear-gradient(170deg, #6F50C9 0%, #FC5343 100%) !important;font-size:.9rem;font-weight:bold}#live .line{padding:0 20px;display:flex;justify-content:center;align-items:center;grid-gap:10px;gap:10px;max-width:450px;color:#ae8f7a}#live .line hr{flex:1 1;border:2px solid #ae8f7a;opacity:1}#live .line svg{font-size:1.5rem}.bg-image{background-position:top}
#galeri{max-width:1000px;background-color:#113468;padding-bottom:130px}#galeri h2{margin:30px 0;color:#fff;font-family:"Great Vibes",cursive;text-align:center;font-size:3rem}#galeri #carouselGaleri{height:450px}@media screen and (max-width: 450px){#galeri #carouselGaleri{height:300px}}#galeri #carouselGaleri .carousel-indicators{height:120px;width:100%;padding-left:10px;padding-right:10px;top:450px;background-color:#113468;overflow:scroll}@media screen and (max-width: 450px){#galeri #carouselGaleri .carousel-indicators{top:300px}#galeri #carouselGaleri .carousel-indicators.scroll-md{overflow:scroll;justify-content:flex-start}}#galeri #carouselGaleri .carousel-indicators button{min-width:120px;width:120px;height:100px}
#amplop{max-width:1000px;width:100%;background-size:cover;background-position:top}#amplop .amplop-container{align-items:center;padding-left:20px;padding-right:20px}#amplop .amplop-container h2{font-family:"Playball",cursive;color:#113468;font-size:2.5rem}#amplop .amplop-container p{color:dimgray;font-family:"Alata",sans-serif;font-size:1.3rem;max-width:750px}#amplop .amplop-container .rekening{width:95%;align-items:center;box-shadow:0px 20px 25px -16px rgba(0,0,0,.5)}#amplop .amplop-container .rekening .divider{width:100%;height:3px;background-color:#c87b3e}#amplop .amplop-container .rekening .btn{background-color:#113468;color:#fff;border-radius:20px;font-size:.9rem}#amplop .amplop-container .rekening .btn svg{font-size:1.2rem}#quote{width:100%;background-color:#113468;color:#fff;padding:70px 0;text-align:center}#quote p{font-family:"Cuprum",sans-serif;font-weight:500;font-size:1.5rem;max-width:800px;margin:0 auto;line-height:1.1}
#ucapan{width:100%;max-width:1000px;background-size:cover;background-position:top;text-align:center}#ucapan .ucapan-container{margin-top:120px;width:80%}@media screen and (max-width: 700px){#ucapan .ucapan-container{margin-top:100px}}#ucapan .ucapan-container h2{font-family:"Philosopher",sans-serif;color:#113468;font-size:3rem}#ucapan .ucapan-container p{font-family:"Roboto Slab",serif;font-size:1.5rem;color:#626262}#ucapan .ucapan-container .form-container{margin-top:100px;display:flex;flex-direction:column;justify-content:center;align-items:center}#ucapan .ucapan-container .form-container form{background-color:#fff;padding:20px;border-top-left-radius:7px;border-top-right-radius:7px;border:1px solid #d9dceb;border-bottom:none}#ucapan .ucapan-container .form-container form input[type=text],#ucapan .ucapan-container .form-container form textarea{padding:10px;width:100%;border-radius:7px;border:1px solid #d9dceb}#ucapan .ucapan-container .form-container form input[type=text]:focus,#ucapan .ucapan-container .form-container form textarea:focus{outline:none;border-color:#64b6ec}#ucapan .ucapan-container .form-container form select{color:#44525f;outline:none}#ucapan .ucapan-container .form-container form input[type=submit]{align-self:flex-start;color:#fff;padding:5px 20px;background-color:#113468;border:none;border-radius:10px;font-size:.9rem;font-weight:600}#ucapan .ucapan-container .form-container .daftar-ucapan{width:100%;background-color:#fff;padding:20px;border-radius:0 0 7px 7px;border:1px solid #d9dceb;margin-bottom:50px}
.batas-penutup{width:100%;height:195px;background-color:#113468;display:flex;flex-direction:column;justify-content:space-between;margin-bottom:-110px;z-index:2}.batas-penutup img{display:block}.batas-penutup .putar{-webkit-transform:rotate(180deg);transform:rotate(180deg)}#penutup{width:100%;max-width:1000px;text-align:center;background-size:cover;background-position:bottom}#penutup img{z-index:3}#penutup .penutup-container{width:100%;justify-content:center;align-items:center;margin-bottom:50px}#penutup .penutup-container p{font-family:"Dosis",sans-serif;font-weight:600;font-size:1.2rem;color:#586472;max-width:700px}#penutup .penutup-container h2{font-family:"Satisfy",cursive;font-size:2rem;color:#091e3e}#penutup .penutup-container h3{font-family:"Playball",cursive;font-size:2.3rem;color:#14346b}#footer{width:100%;height:300px;max-width:1000px;background-color:#113468;display:flex;flex-direction:column;justify-content:center;align-items:center;color:#fff}#footer a{display:block;text-decoration:none;margin:0 auto}#footer a svg{color:#fff;font-size:2rem}
