.batas-penutup {
  width: 100%;
  height: 195px;
  background-color: #113468;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  margin-bottom: -110px;

  z-index: 2;

  img {
    display: block;
  }

  .putar {
    transform: rotate(180deg);
  }
}

#penutup {
  width: 100%;
  max-width: 1000px;
  text-align: center;

  background-size: cover;
  background-position: bottom;

  img {
    z-index: 3;
  }

  .penutup-container {
    width: 100%;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;

    p {
      font-family: 'Dosis', sans-serif;
      font-weight: 600;
      font-size: 1.2rem;
      color: #586472;
      max-width: 700px;
    }

    h2 {
      font-family: 'Satisfy', cursive;
      font-size: 2rem;
      color: #091E3E;
    }

    h3 {
      font-family: 'Playball', cursive;
      font-size: 2.3rem;
      color: #14346b;
    }
  }
}

#footer {
  width: 100%;
  height: 300px;
  max-width: 1000px;
  background-color: #113468;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  color: white;

  a {
    display: block;
    text-decoration: none;
    margin: 0 auto;

    svg {
      color: white;
      font-size: 2rem;
    }
  }
}